.video__button {
  position: absolute;
  content: "";
  top: 34%;
  left: 39.5%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 3;
}

.video__link img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  object-fit: contain;
  z-index: 0;

  @include vp-1199 {
    object-fit: cover;
  }
}

.video__wrapper {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 0;
  padding-bottom: 60%;
}

.video__wrapper iframe {
  position: absolute;
  max-width: 482px;
  max-height: 317px;
  width: 100%;
  height: 100%;
  z-index: 2222;
}